export function checkMetamasInstall() {
  let provider = window.ethereum;
  if (typeof provider !== 'undefined') {
    return metaMaskTexts.connect
  } else {
    return metaMaskTexts.install
  }
}

export async function conectToWallet() {
  let provider = window.ethereum;
  if (provider !== undefined) {
    let Reswallet = provider.request({ method: 'eth_requestAccounts' });
    try {
      let account = await Reswallet
      return account[0]
    } catch (error) {
      console.log(error)
      return "error al conectar"
    }

  }

}

