
import { useState } from 'react';
import { ReactComponent as PenEdit } from '../assets/penEdit.svg'

function Formulario({ datosUser, getDatosUserForm, accion }) {
  const [isEdit, setIsEdit] = useState(false);

  return (
    <div className='profile__form'>
      <div className="container__input">
        <input className='profile__input' type="text" placeholder='Nombre y Apellido'
          name='name'
          value={datosUser.name}
          onChange={(e) => getDatosUserForm(e)}
          disabled={(!isEdit && datosUser.status)}
        />
        {(!isEdit && datosUser.status) ? <PenEdit className='iconPen' onClick={() => setIsEdit(!isEdit)} /> : ''}
      </div>
      <div className="container__input">
        <input className='profile__input' type="email" placeholder='E-mail'
          value={datosUser.email}
          name='email'
          onChange={(e) => getDatosUserForm(e)}
          disabled={(!isEdit && datosUser.status)}
        />
        {(!isEdit && datosUser.status) ? <PenEdit className='iconPen' onClick={() => setIsEdit(!isEdit)} /> : ''}
      </div>
      <div>
        {(!isEdit && datosUser.status) ? '' :
          <button className='boton --form__botton'
            onClick={
              () => {
                accion();
                if (isEdit && datosUser.status) { setIsEdit(!isEdit) }
              }
            }

          >Save
          </button>
        }


        {isEdit ?
          <button className='boton --form__botton'
            onClick={() => setIsEdit(!isEdit)}
          >Cancelar</button> : null
        }
      </div>
    </div>
  );
}

export default Formulario;