import { ReactComponent as Instagram } from "../assets/instagram.svg";
import { ReactComponent as Telegram } from "../assets/telegram.svg";
import { ReactComponent as Discord } from "../assets/discord.svg";
import { ReactComponent as Twitter } from "../assets/twitter.svg";
import { ReactComponent as Linkedin } from "../assets/linkedin.svg";
import Logo from '../assets/png/LogoNFT-OutOfFlameClub.png'
import { useEffect, useState } from "react";
import { checkMetamasInstall, conectToWallet } from "../utils/login"
import { useLocation, useNavigate } from "react-router-dom";
import { conexion } from "../utils/store/slices/blockchainSlice";
import { useDispatch, useSelector } from "react-redux";

export const metaMaskTexts = {
  connect: 'CONNECT YOUR WALLET',
  profile: 'profile',
  install: 'install MetaMask',
  disconnect: 'desconect wallet',
}

export let ancla = ''

const Menu = () => {
  let dispatch = useDispatch();
  let navigate = useNavigate();
  let location = useLocation();
  const [metaMaskBotonText, setMetaMaskBotonText] = useState(metaMaskTexts.connect);
  const [closerMenu, setCloserMenu] = useState(true);

  const blockchain = useSelector((store)=> store.blockchain)

  useEffect(() => {
    let storage = blockchain.account
    if (storage) {
      setMetaMaskBotonText(metaMaskTexts.profile)
    } else {
      let res = checkMetamasInstall()
      setMetaMaskBotonText(res)
    }
  }, []);

  // CONECTAR
  
  async function hadleConect() {
    let res = await conectToWallet()
    if(res === "error al conectar") {
      return ""
    } else {
      localStorage.setItem('account',res)
      setMetaMaskBotonText(metaMaskTexts.profile)
      if(blockchain.account){
        navigate("/profile")
      }
      else{
        dispatch(conexion(dispatch))
      }
    }
  }

  function BotonRedirecionar(name) {
    if (location.pathname === '/profile') {
      navigate('/')
      ancla = name
      setCloserMenu(true)
    }  
    if (location.pathname === '/') {
      let element = document.getElementById(name)
      if (element) {
        setCloserMenu(true)
        element.scrollIntoView({ block: "end", behavior: "smooth" })
      }
    }
    else {
      navigate('/')
      ancla = name
      setCloserMenu(true)
    } 
  }

  return (
    <div className="menu">
      <div className="menu-movil">
        <div
          className="hamburguesa"
          onClick={() => {
            setCloserMenu(!closerMenu);
          }}
        >
          <div className={closerMenu == true ? "bar1" : "bar1 change1"}></div>
          <div className={closerMenu == true ? "bar2" : "bar2 change2"}></div>
          <div className={closerMenu == true ? "bar3" : "bar3 change3"}></div>
        </div>
      </div>

      <div className={closerMenu == false ? "navegation" : " navegation --desaparecer"}>
        <div className="navegation--icon">
          <a href="https://www.instagram.com/oubita_nft/" target="_blank"><Instagram className="icon" /></a>
          <a href="https://t.me/+rDb0O96gqiMxNjU0" target="_blank"><Telegram className="icon" /></a>
          <a href="https://discord.gg/GmUKGBrRHB" target="_blank"><Discord className="icon" /></a>
          <a href="https://twitter.com/Oubita_NFT" target="_blank"><Twitter className="icon" /></a>
          <a href="https://www.linkedin.com/company/oubita/" target="_blank"><Linkedin className="icon" /></a>
        </div>
        <nav className="navegation--list">
          <a className="link" onClick={() => BotonRedirecionar('mint')}>MINT</a>
          <a className="link" onClick={() => BotonRedirecionar('about')}>ABOUT</a>
          <a className="link" onClick={() => BotonRedirecionar('rewards')}>NFT BENEFITS</a>
          <a className="link" onClick={() => BotonRedirecionar('roadmap')}>ROADMAP</a>
          <a className="link" href="https://oubita-project.gitbook.io/oubita-whitepaper/oubita/oubita" target="_blank">WHITEPAPER</a>
          <a className="link" onClick={() => BotonRedirecionar('marketplace')}>MARKETPLACE</a>
          <a className="link" onClick={() => BotonRedirecionar('faqs')}>FAQS</a>
        </nav>
      </div>

      <button className={closerMenu == false ? "boton --botonMenu" : "boton --botonMenu --desaparecer "}
        onClick={() => hadleConect()}
      >
        {metaMaskBotonText}
      </button>

      <div className='logoMenu'>
          <img className='logo' src={Logo} alt="" />
      </div>
    </div>
  );
};

export default Menu;
