import { useEffect, lazy, Suspense } from "react";
// import About from "../componets/About";
// import Faqs from "../componets/Faqs";
// import Marketplace from "../componets/Marketplace";
// import Mint from "../componets/Mint";
// import NftsBenefits from "../componets/NftsBenefits";
// import Roadmap from "../componets/Roadmap";
// import OubitaToken from "../componets/OubitaToken";
import { ancla } from "../componets/Menu";

const LazyAbout = lazy(()=>import("../componets/About"))
const LazyFaqst = lazy(()=>import("../componets/Faqs"))
const LazyMarketplace = lazy(()=>import("../componets/Marketplace"))
const LazyMint = lazy(()=>import("../componets/Mint"))
const LazyNftsBenefits = lazy(()=>import("../componets/NftsBenefits"))
const LazyRoadmap = lazy(()=>import("../componets/Roadmap"))
const LazyOubitaToken = lazy(()=>import("../componets/OubitaToken"))


const Home = ({ }) => {
  useEffect(() => {
    let element = document.getElementById(ancla)
    if (element) {
      element.scrollIntoView({ block: "end", behavior: "smooth" })
    }
  }, []);
  return (
    <main className='content'>
      <Suspense fallback={null}>
        <LazyMint id={'mint'} />
        <LazyAbout id={'about'} />
        <LazyNftsBenefits id={'rewards'} />
        <LazyRoadmap id={'roadmap'} />
        <LazyMarketplace id={'marketplace'} />
        <LazyOubitaToken id={'OUBITATOKEN'} />
        <LazyFaqst id={'faqs'} />
      </Suspense>
    </main>
  );
}

export default Home;