import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { createUserServer, getOnlyUserServer, updateUserServer } from '../apis/requestApi';
import Logo from '../assets/png/nft-logo.png'
import Formulario from '../componets/Formulario';
import Modal from '../componets/Modal';
import NftRender from '../componets/NftRender';
import { useDispatch, useSelector } from 'react-redux';
import { fetching } from '../utils/store/slices/dataSlice';

const Profile = () => {
  const [message, setMessage] = useState(null);
  const [show, setShow] = useState(false);

  const blockchain = useSelector((store)=>store.blockchain)
  const dataUser = useSelector((store)=>store.dataUser)

  let dispatch = useDispatch()
  let LocalAccount = blockchain.account
  let navigate = useNavigate();

  const [datosUser, setDatosUser] = useState({
    username: `${LocalAccount}`,
    name: ``,
    email: ``,
    status: false
  });


  useEffect(() => {
    let res = handleLogin(LocalAccount)
    if (res.account !== null) {
      getDatosUserServer(res.account)
    }
  }, [message])

  const handleLogin = (account) => {
    let res = checkWalletConnection(account)
    return res
  }

  function checkWalletConnection(account) {
    if (account.length > 3) {
      navigate("/profile")
      return { status: true, account }
    }
    else {
      navigate("/")
      return { status: false, account: null }
    }
  }

  async function getDatosUserServer(account) {
    try {
      let res = await getOnlyUserServer(account)
      const { status, data, message } = res
      console.log(res)
      if (status) {
        const { username, name, email } = data
        let obj = {
          username,
          name,
          email,
          status,
        }
        setDatosUser(obj)
      } else {
        console.log(message)
      }
    } catch (error) {
      console.log(error, 'se ha producido un error al intentar traer los datos del user')
    }
  }

  function getDatosUserForm(elemet) {
    let user = datosUser
    const { name, value } = elemet.target
    let newUser = { ...user, [name]: value }
    setDatosUser(newUser)
  }

  function isValidateDatosForm(datosUser) {
    const regExpCorrero = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i

    if (datosUser.username.length === 0) {
      throw {
        status: false,
        message: 'no existe su identificador de usuario por favor vuelva a loguearse'
      }
    }
    if (datosUser.name === '') {
      throw {
        status: false,
        message: 'el campo del nombre no puede estar vacio'
      }
    }
    if (datosUser.name.length <= 3) {
      throw {
        status: false,
        message: 'el campo nombre debe tener mas de 3 caracteres'
      }
    }
    if (!regExpCorrero.test(datosUser.email) || datosUser.email.length === 0) {
      throw {
        status: false,
        message: 'el campo email debe ser valido'
      }
    }
  }

  async function userCreate() {
    try {
      let user = datosUser
      let res
      res = isValidateDatosForm(user)
      res = await createUserServer(user)
      handleMessage(res)

    } catch (error) {
      let res = error
      handleMessage(res)
    }
  }

  async function userEdit() {
    const { name, email, username } = datosUser

    try {
      isValidateDatosForm(datosUser)
      let res = await updateUserServer(username, { name, email })
      handleMessage(res)

    } catch (error) {
      console.log(error)
      handleMessage(error)
    }

  }

  function handleMessage(res) {
    setMessage(res)
    setShow(true)
    setTimeout(() => setShow(false), 2000)
  }

  useEffect(()=>{
        
    fetching(blockchain.account, dispatch, blockchain.contracNFT)
    
    const interval = setInterval(()=>{
      fetching(blockchain.account, dispatch, blockchain.contracNFT)
    }, 10000)

    return()=>clearInterval(interval)
  },[])

  return (
    <div className='profile'>
      <div className='profile__header'>

        <div className='profile__ContainerImg'>
          <img src={Logo} className='img' alt="" />
        </div>
        <div className='profile__Wallet'>
          {datosUser.username}
        </div>
        <Formulario
          datosUser={datosUser}
          getDatosUserForm={getDatosUserForm}
          accion={datosUser.status ? userEdit : userCreate}
        />
      </div>
      <div className='profile__body'>
        <h1 className='profile__body_titile'>My NFTs</h1>
      </div>
      <Modal
        setShow={setShow}
        show={show}
      >
        <h2>{message?.message}</h2>
      </Modal>
      <div className='profile_nft_container'>
                <NftRender data={dataUser}/>
      </div>
    </div>
  );
}

export default Profile;